







































import {
	childMixin
} from "../../../common/mixins.js";
export default {
	data() {
		return {
			totalScore: 0,
			user_atten_list: [{
					imgsrc: '../../../static/img/money.png',
					name: '赚积分',
					target: ''
				},
				{
					imgsrc: '../../../static/img/search.png',
					name: '积分明细',
					target: 'integralDetail'
				}
			],
			user_task_list: [{
				task: '绑定微信',
				task_info: '下次登录更便捷',
				status: 1,
				target: ''
			}, {
				task: '首次参与挑战任务',
				task_info: '挑战任务可获得丰富奖励',
				status: 0,
				target: 'find'
			}, {
				task: '邀请好友加入爱氪世界',
				task_info: '每邀请一个好友可获得一个烟弹',
				status: 0,
				target: 'invitedFriend'
			}, {
				task: '完善个人资料',
				task_info: '完善个人资料可以帮助您更好管理健康数据',
				status: 0,
				target: 'userData'
			}]
		}
	},
	mixins: [childMixin],
	onShow: function() {
		this.initData();
	},
	onPullDownRefresh: function() {
		this.initData();
	},
	methods: {
		target(tag) {
			if (tag == '') {
				return;
			} else if (tag == 'find') {
				uni.switchTab({
					url: '../find/find',
				});
			} else {
				uni.navigateTo({
					url: '../../template/' + tag + '/' + tag
				});
			}
		},
		initData() {
			let that = this;
			this.$api.myScorePage({
				userCode: this.$store.state.userCode,
				token: this.$store.state.token,
			}, function(res) {
				console.log(res);
				uni.stopPullDownRefresh();
				if (res.data.status == 1) {
					that.totalScore = res.data.data.totalScore
				}
				if (res.data.status == 99) {
					that.loginOut(res.data.msg);
				}
			}, function(err) {
				uni.stopPullDownRefresh();
				console.log(err)
			});
			this.$api.addScorePage({
				userCode: this.$store.state.userCode,
				token: this.$store.state.token,
			}, function(res) {
				console.log(res)
				if (res.data.status == 1) {
					that.user_task_list[1].status = res.data.data.activeState;
					that.user_task_list[3].status = res.data.data.infoState;
				}
			}, function(err) {
				console.log(err)
			});
		}
	}
}



